body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.HeroSection{
  display: flex;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.NavBarItems{

    background-color: transparent;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1rem;
    font-weight: 400; 
}

.navbar-logo{

    justify-content: start;
    margin-left: 35px;
    margin-top: 20px;
    cursor: pointer;
    height: 80px;
}

.fa-shopping-bag{

    margin-right: 20px;
    cursor: pointer;
    font-size: 45px;
}

.nav-menu{
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-gap: 5px;
    list-style: none;
    text-align: center;
    width: 60vw;
    margin-right: 1rem;
    font-family: 'Dancing Script', cursive;
    font-size: 1.8rem;
}

.nav-links{
    text-decoration: none;
    color: black;
    cursor: pointer;
}

.nav-links-mobile{
    display: none;
}

.menu-icon{
    display: none;
}

@media screen and (max-width: 960px){
    .NavBarItems{
        position: relative;
    }

    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 500px;
        position: absolute;
        top: 80px;
        left: -100% ;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active{
        left: -12%;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }


    .nav-links{
        text-align: center;
        padding: 1rem; 
        width: 100%;
        display: table;
    }

 .navbar-logo{
     position: absolute;
     top: 10;
     height: 60px;
     left : 20%;
 }

 .fa-shopping-bag{
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-70%, 30%);
 }

 .menu-icon{
     display: block;
     position: absolute;
     top: 0;
     left: 0;
     transform: translate(100%, 60%);
     font-size: 1.8rem;
     cursor: pointer;
 }
}


